<template>
	<div class="content">
		<h1>报名情况总览</h1>
		<hr class="titleHr">
		<div class="container">
			<form>
				<div class="input-group">
				  <span class="input-group-addon" id="sizing-addon1">📱 手机号过滤</span>
				  <input type="text" class="form-control" style="font-size:18px;" placeholder="请输入手机号" aria-describedby="sizing-addon1" onkeyup="value=value.replace(/[^\d]/g,'')" ng-pattern="/[^a-zA-Z]/" v-model="phone" required>
				</div>

				<!-- <div class="input-group" style="margin-top:15px;">
				  <span class="input-group-addon" id="sizing-addon1">🤓 按姓名过滤</span>
				  <input type="text" class="form-control" style="font-size:18px;" placeholder="请输入姓名" aria-describedby="sizing-addon1" v-model="name" v-on:keyup.enter="filterWithName(name)" required>
				</div> -->
			</form>
		</div>

		<div class="result">
			<!-- <hr> -->
			<!-- <p v-if="this.fiterResult.length">kong</p> -->
			<div class="panel panel-default">
				<table class="table">
				    <thead>
				    	<tr>
				    		<th>#</th>
				    		<th>名字</th>
				    		<th>电话</th>
				    		<th>报名课程</th>
				    		<th>是否老学员</th>
				    		<th>预报名时间</th>
				    		<th>地址</th>
				    	</tr>
				    </thead>
				    <tbody>
				    	<tr v-for="student in fiterStudentWithPhone">
				    		<th>{{student.id}}</th>
				    		<td>{{student.name}}</td>
				    		<td>{{student.phone}}</td>
				    		<td>{{student.class}}</td>
				    		<td>
				    			<span v-if="student.isOldStudent">是</span>
				    			<span v-else="student.isOldStudent">否</span>
				    		</td>
				    		<td>{{student.preSigndate}}</td>
				    		<td style="font-size:6px;width:20%;">{{student.address}}</td>
				    	</tr>
				    </tbody>
			  	</table>
		  	</div>
		</div>
		
		<div class="no-result">
			<hr>
			<p>查询时间：<span>{{date | formatDate}}</span></p>
			<img class="qrcode" src="./../assets/utaqi-logo.png">
		</div>
		
		
	</div>

</template>

<script>
	import {formatDate} from './../date.js';
	export default{
		name:"all-students",
		data(){
			return{
				students:[],
				student:{},
				phone:"",
				name:"",
				date:new Date(),
				fiterResult:[]
			}
		},
		methods:{
			filterWithName:function(name){
				if (name=='') {
					// console.log("空号码");
					alert("请输入学员姓名");
					return;
				}else{
					console.log(name);
			  		return this.students.filter((student) => {
			  			return student.name.match(this.name);
			  		})
				}
				
			}
		},
		created(){
			this.$http.get('./../static/students.json')
				.then(function(data){
					// console.log(data);
					return data.json();
				})
				.then(function(data){
					this.students = data;
				})
		},
		computed:{
			fiterStudentWithPhone:function(){
		  		return this.students.filter((student) => {
		  			this.fiterResult = student.phone.match(this.phone);
		  			console.log(this.fiterResult);
		  			return this.fiterResult;//student.phone.match(this.phone);
		  		})
		  	}
		},
		filters: {
		    formatDate(time) {
		      var date = new Date(time);
		      return formatDate(date, 'yyyy-MM-dd hh : mm');
		    }
		}
	}
</script>

<style scoped>
	article{
		padding: 20px 0 40px 0;
		box-sizing: border-box;
	}
	.content h1{
		color: #093f70;
		padding-top: 60px;
		text-align: center;
		/*border-bottom: 1px solid #093f70;*/
		padding: 60px 0 10px 0;
	}
	.titleHr{
		width: 220px;
		max-width: 300px;
		border: none;
		border-top: 5px double #093f70;
		margin-top: 0;
	}
	.container{
		margin: 10px auto;
		max-width: 400px;
	}
	.result{
		margin: 20px auto;
		max-width: 100%;
		min-width: 600px;
	}
	.no-result{
		margin: 10px auto;
		max-width: 400px;
		text-align: center;
	}
	.qrcode{
	    width: 140px;
	    margin: 10px auto;
	}
	td{
		font-size: 12px;
	}
</style>


