<template>
	<div class="content">
		<h1>报名情况查询</h1>
		<hr class="titleHr">
		<div class="container">
			<form>
				<div class="input-group">
				  <span class="input-group-addon" id="sizing-addon1">📱 手机号</span>
				  <input type="text" class="form-control" style="font-size:18px;" placeholder="请输入手机号" aria-describedby="sizing-addon1" onkeyup="value=value.replace(/[^\d]/g,'')" ng-pattern="/[^a-zA-Z]/" v-model="phone" required>
				</div>
				<button class="submitButton" v-on:click.prevent="getInfoWithPhone(phone)">查询</button>
			</form>
		</div>

		<div v-if="isHasResult" class="result">
			<hr>
			<h3>查询结果如下：</h3>
			<ul class="list-group">
			  <li class="list-group-item">
			  	<span style="color:#686868">姓名：</span>
			  	<strong>{{student.name}}</strong>
			  </li>
			  <li class="list-group-item">
			  	<span style="color:#686868">电话：</span>
			  	<strong>{{student.phone}}</strong>
			  </li>
			  <li class="list-group-item">
			  	<span style="color:#686868">报名课程：</span>
			  	<strong>{{student.class}}</strong>
			  </li>
			  <li class="list-group-item">
			  	<span style="color:#686868">是否老学员：</span>
			  	<strong v-if="student.isOldStudent">是</strong>
			  	<strong v-else="student.isOldStudent">否</strong>
			  </li>
			  <li class="list-group-item">
			  	<span style="color:#686868">预报名时间：</span>
			  	<strong>{{student.preSigndate}}</strong>
			  </li>
			  <li class="list-group-item">
			  	<span style="color:#686868">地址：</span>
			  	<strong>{{student.address}}</strong>
			  </li>
			</ul>
		</div>
		
		<div v-if="isNOResult" class="no-result">
			<hr>
			<h3>未查到您的报名信息</h3>
			<p>已经报名未显示结果或新学员报名请联系：<a href="tel:15248184142">15248184142</a></p>
			<img class="qrcode" src="./../assets/anar.png">
		</div>
		
		
	</div>

</template>

<script>
	export default{
		name:"student",
		data(){
			return{
				students:[],
				student:{},
				phone:"",
				isHasResult:false,
				isNOResult:false
			}
		},
		methods:{
			getInfoWithPhone:function(phone){
				if (phone=='') {
					// console.log("空号码");
					alert("请输入您的手机号");
					return;
				}else{
					for(let item of this.students){
						if(item.phone == this.phone){
							this.isHasResult = true;
							this.student = item;
							this.isNOResult = false;
							return;
						}else{
							this.isNOResult = true;
							this.isHasResult = false;
						}
					}
				}
				
			}
		},
		created(){
			this.$http.get('./../static/students.json')
				.then(function(data){
					// console.log(data);
					return data.json();
				})
				.then(function(data){
					this.students = data;
					// console.log(this.students);
				})
		}
	}
</script>

<style scoped>
	article{
		padding: 20px 0 40px 0;
		box-sizing: border-box;
	}
	.content h1{
		color: #093f70;
		padding-top: 60px;
		text-align: center;
		/*border-bottom: 1px solid #093f70;*/
		padding: 60px 0 10px 0;
	}
	.titleHr{
		width: 220px;
		max-width: 300px;
		border: none;
		border-top: 5px double #093f70;
		margin-top: 0;
	}
	.container{
		margin: 10px auto;
		max-width: 320px;
	}
	.submitButton{
		margin: 20px auto;
		width: 100%;
		padding: 8px;
	}
	.result{
		margin: 10px auto;
		max-width: 400px;
	}
	.no-result{
		margin: 10px auto;
		max-width: 400px;
		text-align: center;
	}
	.qrcode{
	    width: 140px;
	    margin: 10px auto;
	}
</style>


