<template>
	<div class="teachers">
		<div class="panel panel-default">
	        <div class="panel-body panel-default">
	          <h2 style="text-align:center;">免费直播讲座</h2>
	        </div>
	        <hr style="margin-top:5px;">
	        <div class="row">
	          <div v-for="teacher in teachers" class="col-sm-6 col-md-3">
	            <div class="thumbnail">
	              <img class="teacher" :src="teacher.avatar" alt="...">
	              <div class="caption">
	                <h4>{{teacher.lecture.name}}</h4>
	                <p>时间：{{teacher.lecture.date}}</p>
	                <p>讲师：{{teacher.name}}</p>
	                <p>
	                  <button class="btn btn-primary" v-on:click="showSummary(teacher)">{{btnTitle}}</button>
	                  <div id="qqg" v-if="isZhankai">
	                    <hr>
	                    <p>
	                      {{teacher.introduction}}
	                    </p>
	                  </div>
	                </p>
	              </div>
	            </div>
	          </div>
	        </div>
      </div>
	</div>

</template>

<script>
	export default{
		name:"teachers",
		data(){
			return{
				teachers:[],
				btnTitle:"展开简介",
			    isZhankai:false
			}
		},
		methods:{
		    showSummary:function(teacher){
		      this.isZhankai = !this.isZhankai;
		      if(this.isZhankai){
		        this.btnTitle = "收起简介";
		      }else{
		        this.btnTitle = "展开简介";
		      }
		    }
		},
		created(){
			this.$http.get('./../static/teachers.json')
		  		.then(function(data){
		  			console.log(data.json());
		        	return data.json(); //json解析
		  		})
		      .then(function(data){
		        this.teachers = data;
		      })
		}
	}
</script>

<style scoped>
	article{
		padding: 20px 0 40px 0;
		box-sizing: border-box;
	}
	h2{
		color: #093f70;
		/*padding-top: 20px;*/
	}
	h4{
		margin: 10px 0 0 0;
		color: #093f70;
	    line-height: 1.5;
	    padding: 10px 0;
	    font-size: 16px;
	}
	.teachers{
		background-color: #f7f8f9;
		margin-top: 0px; 
		border: none;
		margin-bottom: 20px;
	}
	.panel{
		background-color: #f7f8f9;
		border: none;
		margin-bottom: 5px;
	}
	.panel-body{
		border: none;
	}
	.thumbnail{
		border-radius: 0px;
		border: none;
		padding: 30px;
	}
	.row{
		border: none;
		margin-bottom: 40px;
		box-shadow: none;
		padding: 0 10px;
	}
</style>


