<script>
import Teachers from './Teachers'
import Books from './Books'
import Classes from './Classes'
export default {
  name: 'show-blogs',
  components: {
    Teachers,Books,Classes
  },
  data(){
	return{
		blogs:[],
		search:"",
    btnTitle:"展开简介",
    isZhankai:false,
    classes:[]
	 }
  },
  methods:{
    showSummary:function(teacher){
      this.isZhankai = !this.isZhankai;
      if(this.isZhankai){
        this.btnTitle = "收起简介";
      }else{
        this.btnTitle = "展开简介";
      }
    }
  },
  created(){
  	// http://jsonplaceholder.typicode.com/posts
    // https://vue-blog-jun.firebaseio.com/posts.json
    //本地json文件 './../static/posts.json'
  	this.$http.get('./../static/posts.json')
  		.then(function(data){
  			console.log(data.json());
  			//this.blogs = data.body.slice(0,10);
        return data.json(); //json解析
  		})
      .then(function(data){
        var blogsArray = [];
        console.log(data);
        for(let key in data){
          //console.log(data[key]);
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
      })
  },
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) => {
  			return blog.title.match(this.search);
  		})
  	}
  },
  filters:{
  	// "to-uppercase":function(value){
  	// 	return value.toUpperCase();
  	// }
  	//此方法是上面的"to-uppercase":function(value)一样，写法不一样而已
  	toUppercase(value){
  		return value.toUpperCase();
  	}
  },
  //全局的指令改为局部的指令
  directives:{
  	"rainbow":{
  		bind(el,binding,vode){
  			el.style.color = "#" + Math.random().toString(16).slice(2,8);
  		}
  	}
  }
}
</script>

